.App {
  text-align: center;
}

.title{
  font-family: Georgia, 'Times New Roman', Times, serif;
  font-size: 2em;
  background: #aaffee;
  display: inline-block;
  margin-bottom: 1em;
  padding: 0px 20px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  -webkit-box-shadow: 10px 10px 5px rgba(0,0,0,0.05);
          box-shadow: 10px 10px 5px rgba(0,0,0,0.05);
}

.form form{
  
  max-width: 500px;
    padding: 10px 20px;
    background: #f4f7f8;
    margin: 10px auto;
    padding: 20px;
    background: #f4f7f8;
    border-radius: 8px;
    font-family: Georgia, "Times New Roman", Times, serif;
  
}

.form input{
 
  font-family: Georgia, "Times New Roman", Times, serif;
    background: rgba(255,255,255,.1);
    border: none;
    border-radius: 4px;
    font-size: 16px;
    margin: 0;
    outline: 0;
    padding: 7px;
    width: 100%;
    box-sizing: border-box; 
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box; 
    background-color: #e8eeef;
    color:#8a97a0;
    -webkit-box-shadow: 0 1px 0 rgba(0,0,0,0.03) inset;
    box-shadow: 0 1px 0 rgba(0,0,0,0.03) inset;
    margin-bottom: 30px;
    
}

.form input:-webkit-autofill {
  border: 1px solid #ccc;
  -webkit-box-shadow: inset 0 0 0px 9999px white;
}

.form button{
  position: relative;
    display: block;
    padding: 19px 39px 18px 39px;
    color: #FFF;
    margin: 0 auto;
    background: #1abc9c;
    font-size: 18px;
    text-align: center;
    font-style: normal;
    width: 100%;
    border: 1px solid #16a085;
    border-width: 1px 1px 3px;
    margin-bottom: 10px;
}
.form button:hover{
  background: #109177;
  cursor: pointer;
}

.form input:focus{
  background: #d2d9dd;
}
.weather__key{
  
  font-family: Georgia, 'Times New Roman', Times, serif;
  font-size: 48px;
  margin: 0.5em auto;
  max-width: 50%;
  border-bottom: 1px solid #109177;
}
.weather__value{
  display: block;
}


